import React, { Component } from 'react';
import PropTypes from "prop-types"
import { resumeType } from "../../resumeType"
import { getSummary, networkFromProfile } from '../shared/resume';
import df from '../shared/dateFormat'
import { libreCaslonTextBase64 } from '../../../fonts/libreCaslon';

const addDebugBorder = s => {
  return Object.assign(s, {
    //border: "1px black solid"
  })
}

const a4WidthPixels = 780
const a4HeightPixels = 1110
const pagePadTop = 40
const pagePadLeft = 40
const pageWidth = a4WidthPixels - (pagePadLeft * 2)

const leftPercent = 34
const middleGap = 5
const leftRightPadding = 10
const leftWidthPx = (0.01 * leftPercent * pageWidth)
const rightWidthPx = (0.01 * (100 - leftPercent) * pageWidth)

const blackBorder = "1px black solid"

const cvProfileId = "cv-profile"
const cvProfileSummaryId = "cv-profile-summary"
const cvEducationId = "cv-education"
const cvSkillsId = "cv-skills"

const getProfile = (basics) => {
  const web = networkFromProfile(basics, "Web")
  const github = networkFromProfile(basics, "Github")
  // const twitter = networkFromProfile(basics, "Twitter")
  const rows = [
    ["web", web.url],
    ["github", github.username],
    // ["twitter", twitter.username],
  ]
  const tdPadding = {
    padding: 2
  }
  const toRow = (r, i) => (
    <tr key={i} style={tdPadding}>
      <td style={tdPadding}><b>{r[0]}</b></td>
      <td style={tdPadding}>{r[1]}</td>
    </tr>
  )
  return (<div style={{
    width: leftWidthPx - 5,
    leftMargin: 20,
    bottomMargin: 10,
    paddingLeft: 5,
    borderTop: blackBorder,
    borderRight: blackBorder,
    borderBottom: blackBorder,
    borderTopRightRadius: 10,
    borderBottomRightRadius: 10,
  }}>
    <table style={addDebugBorder({
    })}>
      <tbody>
        {rows.map(toRow)}
      </tbody>
    </table>
  </div>)
}

class TitleAndProfile extends Component {
  constructor(props) {
    super(props);

    this.state = {
      summaryHeight: 0,
    };
  }

  componentDidMount() {
    var elem = document.getElementById(cvProfileSummaryId);
    if (elem) {
      var height = elem.offsetHeight;

      if (height !== this.state.summaryHeight) {
        this.setState({
          summaryHeight: height
        })
      }
    }
  }

  render() {
    const height = this.state.summaryHeight
    const basics = this.props.basics
    return (
      <div style={{
        height,
        display: 'inline-flex',
        flexDirection: 'column',
        justifyContent: 'space-between'
      }}>
        <div style={{
          width: leftWidthPx,
          paddingTop: 20,
          textAlign: 'center',
        }}>
          <span style={{
            fontSize: 30,
          }}>{basics.name}</span>
        </div>
        {getProfile(basics)}
      </div>
    );
  }
}

const nameAndProfile = basics => {
  const pStyle = {
    marginTop: 10,
    marginBottom: 0,
  }
  const summaryParts = getSummary(basics)
    .split('\n')
    .filter(x => x !== "")
    .map((p, i) => <p style={pStyle} key={i}>{p}</p>)

  return (
    <div id={cvProfileId} style={addDebugBorder({
      width: pageWidth,
    })}>
      <div style={{
        width: leftWidthPx,
        display: "inline",
      }}>

        <TitleAndProfile basics={basics} />

      </div>

      <div
        id={cvProfileSummaryId}
        style={{
          float: 'right',
          width: rightWidthPx - 1 - middleGap - leftRightPadding,
          textAlign: "justify",
          marginBottom: 0,
          marginRight: 0,
          paddingLeft: leftRightPadding,
          paddingBottom: 10,
          borderBottom: blackBorder,
          borderLeft: blackBorder,
          borderBottomLeftRadius: 10,
        }}>
        
        {summaryParts}
      </div>
    </div>
  )
}

const education = jsonResume => {
  var toEducation = function (edu, i) {
    let key = edu.institution + i
    var fStart = df.formatDate(edu.startDate);
    var fEnd = df.formatDate(edu.endDate);

    return (<div key={key}>
      <div style={{
        padding: 5,
        marginBottom: 15,
      }} >
        <span><b>{edu.institution}</b></span>
        <div style={{
          paddingTop: 5,
          textAlign: 'right'
        }}>
          <span><i>{fStart} - {fEnd}</i></span >
        </div>
        <div style={{
          width: "80%",
          paddingTop: 5,
          textAlign: 'initial'
        }}>
          <span>{edu.studyType}</span>
        </div>
        <div style={{
          paddingTop: 5,
          textAlign: 'right'
        }}>
          <span style={{}}><u>{edu.gpa}</u></span>
        </div>
      </div>
    </div>);
  };

  return (
    <div id={cvEducationId} style={addDebugBorder({
    })}>
      <div style={{
        textAlign: "justify",
      }}>
        <div style={{
          textAlign: 'right',
          paddingBottom: 10,
        }}>
          <span><b>Education</b></span>
        </div>
        <div>
          {jsonResume.education.map((x, i) => toEducation(x, i))}
        </div>
      </div>
    </div>
  )
}

const skillsComponent = (theProps) => {
  
  const skills = theProps.jsonResume.skills
  const skillsObj = theProps.skills

  const keyword = (keywordId, isFinal) => {
    var keyWordName = skillsObj.skills[keywordId].name
    var commaOrBlank = isFinal ? "" : ","
    return (
      <span key={keywordId}>{keyWordName + commaOrBlank} </span>
    );
  }

  const skill = (theSkill) => {
    return (
      <div style={{
        padding: 5,
      }}>
        <b>{theSkill.name}</b> - {theSkill.keywords.map((x, i) => keyword(x, i === (theSkill.keywords.length - 1)))}
      </div>
    )
  }

  return (
    <div id={cvSkillsId} style={addDebugBorder({
    })}>
      <div style={{
        textAlign: "justify",
      }}>
        <div style={{
          textAlign: 'right',
          paddingBottom: 10,
        }}>
          <span><b>Skills</b></span>
        </div>
        <div>
          {skills.map(x => <div key={x.name}>{skill(x)}</div>)}
        </div>
      </div>
    </div>
  )
}


class EducationAndSkills extends Component {
  constructor(props) {
    super(props);

    this.state = {
      leftHeight: 0,
      rightHeight: 0,
    };
  }

  componentDidMount() {
    var educationElement = document.getElementById(cvEducationId);
    var skillsElement = document.getElementById(cvSkillsId);

    if (educationElement && skillsElement) {
      var leftHeight = educationElement.offsetHeight;
      var rightHeight = skillsElement.offsetHeight;
      const isDifferent = this.state.leftHeight !== leftHeight || this.state.rightHeight !== rightHeight
      if (isDifferent) {
        this.setState({
          leftHeight,
          rightHeight
        })
      }
    }
  }

  render() {
    const height = this.state.leftHeight > this.state.rightHeight ? this.state.leftHeight : this.state.rightHeight

    return (
      <div style={{
        display: "inline-block",
        width: pageWidth,
      }}>
        <div style={{
          height,
          display: "inline-block",
          float: "left",
          marginTop: middleGap,
          marginRight: 0,
          marginLeft: 0,
          width: leftWidthPx - 1 - 5,
          borderTop: blackBorder,
          borderBottom: blackBorder,
          borderRight: blackBorder,
          borderTopRightRadius: 10,
          borderBottomRightRadius: 10,
          paddingRight: 5,
          paddingBottom: 10,
          paddingTop: leftRightPadding,
        }}>

          {education(this.props.theProps.jsonResume)}

        </div>
        <div style={{
          height,
          display: 'inline',
          float: 'right',
          width: rightWidthPx - 1 - middleGap - leftRightPadding,
          borderTop: blackBorder,
          borderLeft: blackBorder,
          borderBottom: blackBorder,
          borderTopLeftRadius: 10,
          borderBottomLeftRadius: 10,
          padding: 10,
          paddingRight: 0,
          marginTop: middleGap,
          marginLeft: 5,
          marginRight: 0,
        }}>

          {skillsComponent(this.props.theProps)}

        </div>
      </div>
    );
  }
}

const experience = (jsonResume) => {

  const toWorkplaceTitle = work => {
    var startDateFormatted = df.formatDate(work.startDate);
    var endDateFormatted = work.endDate
      ? df.formatDate(work.endDate)
      : "Present";
    var diff = df.formatDiff(work.startDate, work.endDate);
    return (
      <div style={{
        display: "flex",
        justifyContent: "space-between"
      }}>
        <div >
          <span><b>{work.company}</b></span> - <span><i>{work.position}</i></span>
        </div>
        <div >
          <span>{startDateFormatted}</span> - <span>{endDateFormatted}</span>
          <span><i>  ({diff})</i></span>
        </div>
      </div>
    )
  }
  const toWorkplaceDescription = work => {
    return (
      <div>
        {<div style={{
          padding: 5,
          textAlign: "justify"
        }}>
          {work.summary}
        </div>}

      </div>
    )
  }

  const toHighlights = work => {
    var toHighlight = function (h, i) {
      return <li key={i} ><div style={{
        padding: 3
      }} >{h}</div></li>
    }
    return (
      <div>
        {<div style={{
          textAlign: "justify"
        }}>
          <ul type="square" style={{
            marginTop: 0,
            marginBottom: 0
          }} >
            {work.highlights.map((x, i) => toHighlight(x, i))}
          </ul>
        </div>}

      </div>
    )
  }

  const toWorkplace = work => {
    var containerStyle = Object.assign({}, {
      padding: 10,
      margin: 10
    })
    return (
      <div >
        <div
          style={containerStyle}
        >
          {toWorkplaceTitle(work)}
          {toWorkplaceDescription(work)}
          {work.highlights && work.highlights.length > 0 && toHighlights(work)}

        </div>
      </div>
    );
  }

  return (
    <div style={{
      marginTop: 5,
    }}>
      <div style={{
        textAlign: 'right',
        padding: 10,
        paddingBottom: 0,
        borderTop: blackBorder,
      }}>
        <span><b>Experience</b></span>
      </div>
      <div style={{

      }}>
        <div>{
          jsonResume.work.map(x => toWorkplace(x))
        }
        </div>
      </div>
    </div>
  )
}

class Default extends Component {
  render() {
    return (
      <div>
        <style type="text/css" dangerouslySetInnerHTML={{
          __html: `
      @font-face {
        font-family: 'Libre Caslon Text';
        src: url(data:application/x-font-ttf;base64,${libreCaslonTextBase64});
      }`}} />
        <style type="text/css" dangerouslySetInnerHTML={{
          __html: `body { font-family: 'Libre Caslon Text' }`}} />

        <div style={addDebugBorder({
          width: pageWidth,
          paddingLeft: pagePadLeft,
          paddingRight: pagePadLeft,
          paddingTop: pagePadTop,
        })}>
          {nameAndProfile(this.props.jsonResume.basics)}
          <EducationAndSkills theProps={this.props} />
          {experience(this.props.jsonResume)}
        </div>
      </div>
    );
  }
}

Default.propTypes = {
  jsonResume: resumeType.isRequired,
  skills: PropTypes.object,
  cvWidth: PropTypes.number,
}

export default Default;
