import * as React from 'react'
import SimpleWeb from './themes/simple/web'
import SimplePrint from './themes/simple/print'
import Compact from './themes/compact'

export default function (props) {
  const p = props
  const get = () => {
    if (p.theme === "simple-print") {
      return (
        <SimplePrint
          jsonResume={props.jsonResume}
          skills={props.skills}
          cvWidth={props.cvWidth}
          cvType={props.cvType}
          logoPrefix={props.logoPrefix}
        />
      )
    }
    if (p.theme === "compact") {
      return (
        <Compact
          jsonResume={props.jsonResume}
          skills={props.skills}
          cvWidth={props.cvWidth}
          cvType={props.cvType}
          logoPrefix={props.logoPrefix}
        />
      )
    }
    return (
      <SimpleWeb
        jsonResume={props.jsonResume}
        skills={props.skills}
        cvWidth={props.cvWidth}
        cvType={props.cvType}
        logoPrefix={props.logoPrefix}
      />
    )
  }

  return get()
}
