
export const getSummary = (basics, cvType) => {
  // const basics = jsonResume.basics
  var summary = basics.summary;
  var summaryOverride = basics["summary-" + cvType];
  if (summaryOverride) {
    summary = summaryOverride;
  }
  return summary
}

export const networkFromProfile = (basics, name) => {
  const withName = basics.profiles.filter(x => x.network === name)
  if(withName.length === 0){
    return null
  }
  return withName[0]
}

